.container {
    position: fixed;
    bottom: 3.5em;
    width: calc(100vw - 2.4em);
    height: auto;
    margin: 0;
    padding: 1em .5em;
    background-color: #1c1c1e;
    border-radius: 10px;
}

.form {
    display: flex;
    gap: 1em;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.formInput {
    border: 3px solid transparent;
    outline: none;
    width: 100%;
    padding: 0 1em;
    border-radius: 50px;
    background-color: #fefefc;
    color: #000;
    font-size: 1.25rem;
}

.formInput:focus {
    border: 3px solid #20B2AA;
}

.formBtns {
    padding: .1em 1em;
    font-size: 1.125rem;
    border: 1px solid #20B2AA;
    background-color: #20B2AA;
    border-radius: 50px;
    width: 4em;
}

.formBtns:active {
    background-color: #146A6A;
    border-color: #146A6A;
}

/* Button Icons */

.btnIcons {
    width: 1.7em;
    height: 1.7em;
    color: #fefefc;
    width: 100%;
}

.btnIcons:active {
    color: #1c1c1e;
}